@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.hero-container {
    text-align: center;
    padding: 60px 20px;
    background-color: var(--background-color);
}

.hero-image {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    border: 4px solid var(--divider-color);
    margin-bottom: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
}

.hero-image:hover {
    transform: scale(1.05);
}

h1 {
    font-size: 2.8rem;
    margin-bottom: 10px;
    color: var(--heading-color);
}

h1 .highlight {
    color: var(--button-bg);
}

.subheading {
    font-size: 1.3rem;
    color: var(--text-color);
    margin-bottom: 20px;
}

.cta-button {
    font-size: 1.2rem;
    padding: 12px 25px;
    background-color: var(--button-bg);
    border: none;
    border-radius: 25px;
    color: var(--button-text);
    cursor: pointer;
    transition: transform 0.3s ease, background-color 0.3s ease;
    margin-top: 20px;
}

.cta-button:hover {
    background-color: var(--divider-color);
    transform: scale(1.05);
}

.about-container {
    text-align: center;
    padding: 60px 20px;
}

.about-container h2 {
    font-size: 2rem;
    margin-bottom: 20px;
    color: var(--heading-color);
}

.about-content p {
    font-size: 1.1rem;
    color: var(--text-color);
    margin-bottom: 20px;
}

.divider {
    margin: 20px 0;
    text-align: center;
    font-size: 1.5rem;
    color: var(--divider-color);
}

.contact-container {
    text-align: center;
    padding: 40px 20px;
    margin-top: 30px;
}

.contact-container h2 {
    font-size: 2rem;
    margin-bottom: 20px;
    color: var(--heading-color);
}

.contact-container p {
    font-size: 1.1rem;
    margin-bottom: 30px;
    color: var(--text-color);
}

.contact-container form {
    max-width: 600px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 15px;
    gap: 15px;
}

.contact-container input,
.contact-container textarea {
    width: 100%;
    padding: 12px;
    font-size: 1rem;
    border: 1px solid var(--divider-color);
    border-radius: 5px;
    background-color: #FFF;
    color: var(--text-color);
}

.contact-container textarea {
    height: 120px;
    resize: none;
}

.contact-container button {
    background-color: var(--button-bg);
    color: var(--button-text);
    padding: 10px 30px;
    font-size: 0.95rem;
    border-radius: 4px;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.contact-container button:hover {
    background-color: var(--divider-color);
    transform: scale(1.05);
}

.social-icons {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
    margin-top: 20px;
}

.social-icons a {
    font-size: 1.2rem;
    color: var(--heading-color);
    text-decoration: none;
    display: flex;
    align-items: center;
    grid-gap: 8px;
    gap: 8px;
    transition: color 0.3s ease, transform 0.3s ease;
}

.social-icons a:hover {
    color: var(--divider-color);
    transform: translateY(-3px);
}

.social-icons span {
    font-size: 1rem;
    color: var(--text-color);
}

#footer {
    background-color: var(--button-bg);
    color: var(--button-text);
    text-align: center;
    padding: 20px;
}

/* Import Google Fonts */

/* Root Variables for Theme Colors */
:root {
    --background-color: #FAF4EF;
    --text-color: #6D4E34;
    --heading-color: #8B5E42;
    --button-bg: #A87C60;
    --button-text: #FAF4EF;
    --divider-color: #C4A89E;
}


/* Global Styles */
body {
    font-family: 'Poppins', sans-serif; /* Modern and clean */
    background-color: var(--background-color);
    color: var(--text-color);
    margin: 0;
    padding: 0;
    line-height: 1.6;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
    font-weight: 600;
    color: var(--heading-color);
    letter-spacing: 0.5px;
}

p {
    margin: 0;
    font-size: 1.1rem;
    color: var(--text-color);
}

a {
    color: var(--heading-color);
    text-decoration: none;
    font-weight: 500;
    transition: color 0.3s ease;
}

a:hover {
    color: var(--divider-color);
    text-decoration: underline;
}

/* Button Styles */
button {
    font-family: 'Poppins', sans-serif;
    font-size: 1rem;
    font-weight: 600;
    padding: 10px 20px;
    background-color: var(--button-bg);
    color: var(--button-text);
    border: none;
    border-radius: 25px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

button:hover {
    background-color: var(--divider-color);
    transform: scale(1.05);
}

button:focus {
    outline: none;
    box-shadow: 0 0 8px rgba(200, 168, 150, 0.7);
}

/* Utility Classes */
.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.text-center {
    text-align: center;
}

.mt-20 {
    margin-top: 20px;
}

.mb-20 {
    margin-bottom: 20px;
}

/* Divider */
.divider {
    margin: 30px 0;
    text-align: center;
    font-size: 1.5rem;
    color: var(--divider-color);
}

/* Responsive Design */
@media (max-width: 768px) {
    h1 {
        font-size: 2rem;
    }

    button {
        font-size: 0.9rem;
        padding: 8px 16px;
    }

    .container {
        padding: 0 10px;
    }
}

